<template>
  <div class="md:hidden flex justify-between items-start pt-6">
    <div @click="openSidebar" class="bars"><img src="@/assets/img/icons/menu.svg" alt=""></div>
    <transition name="diagonala" appear>
      <div class="bg-orangeBlack absolute right-0 small-menu p-2 mr-4">
        <div class="items-center gap-2 flex">
          <p class=" text-[14px] font-semibold text-orangeWhite tracking-wide">{{ userName }}
          </p>
          <img @click="isOpenMenu = !isOpenMenu" class="mr-1 cursor-pointer"
               src="@/assets/img/icons/chevron-down.svg" alt="">
        </div>
        <div v-if="isOpenMenu">
          <ul class="cursor-pointer text-orangeWhite/80 text-[13px]">
            <li class="hover:text-orangeWhite transition-all duration-100">Edit banner</li>
            <li @click="removeAll" class="hover:text-orangeWhite transition-all duration-100">Change user</li>
          </ul>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import {useMainStore} from "@/store";

export default {
  data() {
    return {
      isOpenMenu: false
    }
  },
  computed: {
    userName() {
      useMainStore().loadUserName()
      return useMainStore().getName
    }
  },
  methods: {
    openSidebar() {
      this.$emit('open-sidebar')
    },
    removeAll() {
      localStorage.clear()
      useMainStore().setIsLoggedInFalse()
      location.reload();
    },
  }
}
</script>

<style>
.small-menu {
  border-radius: 12px 12px 12px 12px;
}
</style>